import React, {Component} from 'react'
import { Chart } from "react-google-charts";

const fetch = require('node-fetch');

export class Graphique extends Component {
    constructor(props) {
        super(props);
        this.state = {
            graphique : [],
            nbUPMax : 0,
            nbEnseignantsMax: 0
        };
    }

    componentDidMount() {
        const url = 'http://obiwan2.univ-brest.fr:7155/uniteParProf';

        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        };
        
        fetch(url, requestOptions)
        .then(response => response.json())
        .then(data => this.setState({graphique : data.graphique, 
            nbEnseignantsMax : data.infos.nbEnseignantsMax, nbUPMax : data.infos.nbUPMax}))
        .catch(error => console.error(error));
    }


    render() {
        console.log(this.state.nbUPMax)
        return (
            <div className={"centerTableau"}>
                <div style={{ display: 'flex', maxWidth: 900}}>
                    <Chart
                        width={1000}
                        height={300}
                        chartType="ColumnChart"
                        loader={<div className={"center"}><img alt="loader" src="data:image/gif;base64,"></img></div>}
                        data={this.state.graphique}
                        options={{
                            colors : ['#36304a'],
                            chartArea: { width: '50%' },
                            hAxis: {
                                title: 'Nombre d\'unités pédagogiques',
                                minValue: 0,
                                maxValue : this.state.nbUPMax + 1
                            },
                            vAxis: {
                                title: 'Nombre d\'enseignants',
                                maxValue : this.state.nbEnseignantsMax + 1
                            },
                        }}
                        legendToggle={false}
                    />
                </div>
            </div>
        )
    }
}

export default Graphique


